import "./App.css";
import Navbar from "./pages/Navbar";
import { useLocation } from "react-router-dom";
import Hero from "./pages/Hero";
import Skill from "./pages/Skill";
import Projects from "./pages/Project";
import React, { useState } from "react";
import Articles from "./pages/Articles";
import Journey from "./pages/Journey";
import { Helmet } from "react-helmet";

function App() {
  const location = useLocation();
  const heroRef = useState(React.createRef());
  const skillRef = useState(React.createRef());
  const projectRef = useState(React.createRef());
  const articleRef = useState(React.createRef());
  const journeyRef = useState(React.createRef());
  return (
    <>
      <div>
        <Helmet>
          <link
            rel="shortcut icon"
            type="image/png"
            href="./assets/icon/icon.png"
          />
        </Helmet>
      </div>
      <Navbar
        routePath={location.pathname}
        heroRef={heroRef}
        skillRef={skillRef}
        projectRef={projectRef}
      />
      {/* <Outlet /> */}
      <Hero refs={heroRef} />
      <Skill refs={skillRef} />
      <Journey refs={projectRef} />
      <Articles refs={articleRef} />
      <Projects refs={projectRef} />
    </>
  );
}

export default App;
